export function initMagnetic() {
  const magneticEls = document.querySelectorAll('[data-magnetic]') as NodeListOf<HTMLElement>

  magneticEls.forEach((el) => {
    const strength = parseFloat(el.dataset.magneticStrength || '0.2')
    const speed = parseFloat(el.dataset.magneticSpeed || '0.15')

    let mouseX = 0
    let mouseY = 0
    let elX = 0
    let elY = 0

    const update = () => {
      const rect = el.getBoundingClientRect()
      const centerX = rect.left + rect.width / 2
      const centerY = rect.top + rect.height / 2
      const dx = mouseX - centerX
      const dy = mouseY - centerY
      elX += (dx * strength - elX) * speed
      elY += (dy * strength - elY) * speed
      el.style.transform = `translate(${elX}px, ${elY}px)`
      requestAnimationFrame(update)
    }

    el.addEventListener('mouseenter', () => {
      window.addEventListener('mousemove', onMouseMove)
      update()
    })

    el.addEventListener('mouseleave', () => {
      window.removeEventListener('mousemove', onMouseMove)
      elX = 0
      elY = 0
      el.style.transform = 'translate(0, 0)'
    })

    const onMouseMove = (e: MouseEvent) => {
      mouseX = e.clientX
      mouseY = e.clientY
    }
  })
}
