/* ==============================================
Infinite Horizontal Gallery
============================================== */

export function initInfinityGallery() {
  class InfiniteHorizontalScroll {
    constructor(container) {
      if (!container) return;
      this.container = container;
      this.items = Array.from(this.container.children);
      if (this.items.length === 0) return;
      this.scrollX = 0;
      this.smoothScrollX = 0;
      this.touchStartY = 0;
      this.touchDeltaY = 0;
      this.previousDeltaY = 0;
      this.cloneItems();
      this.calculateDimensions();
      this.init();
    }

    cloneItems() {
      const fragmentBefore = document.createDocumentFragment();
      const fragmentAfter = document.createDocumentFragment();
      this.items.forEach((item) => {
        const cloneBefore = item.cloneNode(true);
        const cloneAfter = item.cloneNode(true);
        fragmentBefore.appendChild(cloneBefore);
        fragmentAfter.appendChild(cloneAfter);
      });
      this.container.insertBefore(fragmentBefore, this.container.firstChild);
      this.container.appendChild(fragmentAfter);
    }

    calculateDimensions() {
      this.totalWidth = 0;
      Array.from(this.container.children).forEach((item) => {
        const itemWidth = item.getBoundingClientRect().width;
        const itemMarginRight = parseFloat(getComputedStyle(item).marginRight);
        this.totalWidth += itemWidth + itemMarginRight;
      });
      const originalWidth = this.totalWidth / 3;
      this.scrollX = originalWidth;
      this.smoothScrollX = originalWidth;
      this.container.style.transform = `translateX(${-this.scrollX}px)`;
    }

    init() {
      this.bindEvents();
      this.animate();
      window.addEventListener('resize', () => {
        this.calculateDimensions();
      });
    }

    bindEvents() {
      document.addEventListener('wheel', (e) => this.handleWheel(e), { passive: false });
      document.addEventListener('touchstart', (e) => this.handleTouchStart(e), { passive: true });
      document.addEventListener('touchmove', (e) => this.handleTouchMove(e), { passive: false });
      document.addEventListener('touchend', () => this.handleTouchEnd());
    }

    handleWheel(event) {
      if (event.target.closest('button, input, textarea, select')) return;
      event.preventDefault();
      this.scrollX += event.deltaY * 1.5;
      this.handleInfiniteScroll();
    }

    handleTouchStart(event) {
      this.touchStartY = event.touches[0].clientY;
      this.touchDeltaY = 0;
      this.previousDeltaY = 0;
    }

    handleTouchMove(event) {
      event.preventDefault();
      if (window.innerWidth < 750) {
        const touchY = event.touches[0].clientY;
        this.touchDeltaY = touchY - this.touchStartY;
        const touchSpeed = 4;
        this.scrollX -= (this.touchDeltaY - this.previousDeltaY) * touchSpeed;
        this.previousDeltaY = this.touchDeltaY;
      }
      this.handleInfiniteScroll();
    }

    handleTouchEnd() {
      this.touchDeltaY = 0;
      this.previousDeltaY = 0;
    }

    handleInfiniteScroll() {
      const originalWidth = this.totalWidth / 3;
      if (this.scrollX < 0) {
        this.scrollX += originalWidth;
        this.smoothScrollX += originalWidth;
      } else if (this.scrollX > this.totalWidth - originalWidth) {
        this.scrollX -= originalWidth;
        this.smoothScrollX -= originalWidth;
      }
    }

    animate() {
      this.smoothScrollX += (this.scrollX - this.smoothScrollX) * 0.06;
      this.container.style.transform = `translateX(${-this.smoothScrollX}px)`;
      requestAnimationFrame(() => this.animate());
    }
  }
  new InfiniteHorizontalScroll(document.querySelector('.slider-container'));
}

export function initHandleInfiniteScroll() {
  function initMouseWheelEffects() {
    if (window.innerWidth <= 500) return;

    const selector = '.slider-img-wrap';
    if (!document.querySelector(selector)) return;

    const options = {
      threshold: 100,
      friction: 0.91,
      scaleFactor: 0.004,
      maxScale: 0.5,
      moveFactor: 0.2,
      maxMove: 40,
      velocityMultiplier: 0.05,
    };

    let lastY = 0;
    let velocity = 0;
    let isScrolling = false;
    let touchStartTime = 0;
    let frameId = null;

    function updateEffect() {
      if (!isScrolling) return;

      velocity *= options.friction;

      if (Math.abs(velocity) < 0.1) {
        isScrolling = false;
        velocity = 0;
        cancelAnimationFrame(frameId);
        return;
      }

      const scaleFactor = 1 - Math.min(Math.abs(velocity) * options.scaleFactor, options.maxScale);
      const horizontalMove =
        Math.min(Math.abs(velocity) * options.moveFactor, options.maxMove) *
        (velocity > 0 ? 1 : -1);

      gsap.to(selector, {
        scale: scaleFactor,
        x: horizontalMove,
        duration: 1,
        ease: 'power3.out',
      });

      frameId = requestAnimationFrame(updateEffect);
    }

    function handleWheelScroll(delta) {
      velocity += delta * options.velocityMultiplier;
      if (!isScrolling) {
        isScrolling = true;
        frameId = requestAnimationFrame(updateEffect);
      }
    }

    function handleTouchMove(e) {
      const currentY = e.touches[0].clientY;
      const deltaTime = Date.now() - touchStartTime;
      touchStartTime = Date.now();

      const delta = currentY - lastY;
      lastY = currentY;
      velocity += (delta / deltaTime) * options.velocityMultiplier * 10;

      if (!isScrolling) {
        isScrolling = true;
        frameId = requestAnimationFrame(updateEffect);
      }

      e.preventDefault();
    }

    function handleTouchStart(e) {
      lastY = e.touches[0].clientY;
      touchStartTime = Date.now();
      velocity = 0;
    }

    function handleTouchEnd() {}

    window.addEventListener('wheel', (e) => handleWheelScroll(e.deltaY), { passive: true });
    window.addEventListener('touchstart', handleTouchStart, { passive: false });
    window.addEventListener('touchmove', handleTouchMove, { passive: false });
    window.addEventListener('touchend', handleTouchEnd);
  }

  initMouseWheelEffects();
}

/* ==============================================
Page GSAP Animations
============================================== */

export function initGsapAnimations() {
  const item = document.querySelectorAll('.slider-item');
  if (!item) return;

  if (window.innerWidth < 500) {
    gsap.fromTo(
      item,
      {
        clipPath: 'inset(0 0 100% 0)', // Start with full clipping
        x: 2000, // Set x to 500 for mobile
        yPercent: 100,
        opacity: 1,
        transformOrigin: 'center center',
      },
      {
        clipPath: 'inset(0 0 0% 0)', // Unclipped to reveal the entire element
        x: 0, // Animate to original position
        yPercent: 0,
        opacity: 1,
        duration: 2, // Duration of the animation
        ease: 'expo.inOut', // Smooth easing effect
        stagger: 0.05, // Stagger the animation for each slide
      }
    );
  } else {
    gsap.fromTo(
      item,
      {
        clipPath: 'inset(0 0 100% 0)', // Start with full clipping
        x: 3000, // Set x to 3000 for larger screens
        yPercent: 100,
        opacity: 1,
        transformOrigin: 'center center',
      },
      {
        clipPath: 'inset(0 0 0% 0)', // Unclipped to reveal the entire element
        x: 0, // Animate to original position
        yPercent: 0,
        opacity: 1,
        duration: 2, // Duration of the animation
        ease: 'expo.inOut', // Smooth easing effect
        stagger: 0.05, // Stagger the animation for each slide
      }
    );
  }

  // gsap.fromTo(
  // ".image-container",
  // {

  //   x: 3000, // Initial position off the screen to the right
  //   yPercent: 100,
  //   opacity:0,
  //   transformOrigin: "top center",
  // },
  // {

  //   x: 0, // Animate to original position
  //   yPercent: 0,
  //   opacity:1,
  //   delay:-0.5,
  //   duration: 2, // Duration of the animation
  //   ease: "expo.inOut", // Smooth easing effect
  //   stagger: 0.090, // Stagger the animation for each slide
  // }
  // );

  // gsap.set(".hero .video-thumb video", {
  // clipPath: "inset(100% 100% 0 0)"
  // });

  // gsap.to(".hero .video-thumb video", {
  // clipPath: "inset(0% 0% 0 0)",
  // duration: 2,
  // delay: 0.5,
  // ease: "expo.inOut",
  // });

  // gsap.from(".studio-headline img", {
  // opacity: 0,
  // y: 200,
  // scale: 0.3,
  // rotate: 15,
  // duration: 2,
  // delay: 0.5,
  // ease: "power4.out",
  // })

  const easeSetting = 'power4.out';
  const animationDuration = 0.8;

  gsap.utils.toArray(item).forEach((slide, _, allSlides) => {
    const heading = slide.querySelector('.slider-name-text');
    const image = slide.querySelector('img');

    // Set awal
    gsap.set(heading, { yPercent: 200, rotate: 3, clipPath: 'inset(0% 0% 100% 0%)' });
    gsap.set(image, { scale: 1, filter: 'grayscale(0%)' });

    const enterAnim = gsap
      .timeline({ paused: true })
      .to(heading, {
        yPercent: 0,
        rotate: 0,
        clipPath: 'inset(0% 0% -8% 0%)',
        duration: animationDuration,
        ease: easeSetting,
      })
      .to(image, { scale: 1.3, rotate: 2, duration: animationDuration, ease: easeSetting }, '<');

    const exitAnim = gsap
      .timeline({ paused: true })
      .to(heading, {
        yPercent: 200,
        rotate: 3,
        clipPath: 'inset(0% 0% 100% 0%)',
        duration: animationDuration,
        ease: easeSetting,
      })
      .to(image, { scale: 1, rotate: 0, duration: animationDuration, ease: easeSetting }, '<');

    // Hover in
    slide.addEventListener('mouseenter', () => {
      if (exitAnim.isActive()) exitAnim.progress(1);
      enterAnim.restart();

      // Semua slide lain jadi grayscale
      allSlides.forEach((other) => {
        if (other !== slide) {
          gsap.to(other.querySelector('img'), {
            filter: 'grayscale(100%)',
            duration: animationDuration,
            ease: easeSetting,
          });
        }
      });
      // Slide aktif tetap normal
      gsap.to(image, { filter: 'grayscale(0%)', duration: animationDuration, ease: easeSetting });
    });

    // Hover out
    slide.addEventListener('mouseleave', () => {
      if (enterAnim.isActive()) enterAnim.progress(1);
      exitAnim.restart();

      // Semua kembali normal
      allSlides.forEach((other) => {
        gsap.to(other.querySelector('img'), {
          filter: 'grayscale(0%)',
          duration: animationDuration,
          ease: easeSetting,
        });
      });
    });
  });
}

/* ==============================================
Split Text Animations
============================================== */

export function initSplitTextAnimations() {
  // Clean up existing ScrollTriggers before initializing new ones
  if (gsap.ScrollTrigger) {
    gsap.ScrollTrigger.getAll().forEach((trigger) => trigger.kill());
  }

  // Initialize SplitText for new content
  const newContent = document.querySelector('body');
  if (newContent) {
    const elements = newContent.querySelectorAll('[slider-text-load]');
    const btnLinks = newContent.querySelectorAll('.btn-link');
    const themeToggle = newContent.querySelectorAll('.theme-switch');

    if (elements.length === 0) return;

    elements.forEach((element) => {
      const splitText = new SplitText(element, {
        type: 'lines',
        linesClass: 'line',
        mask: 'lines',
      });
      splitText.lines.forEach((line) => {
        line.style.display = 'inline-block';
        line.style.width = '100%';
        line.style.lineHeight = 'unset';
        line.style.visibility = 'hidden';
      });
      // Force a reflow to ensure styles are applied
      splitText.lines.forEach((line) => line.offsetWidth);
      gsap.set(splitText.lines, {
        visibility: 'visible',
        yPercent: 100,
        clipPath: 'inset(0% 0% 120% 0%)',
        opacity: 1,
      });

      // Check if the current page is the home page
      //   const isHomePage = window.location.pathname === "/" || window.location.pathname === "/index.html";

      gsap.to(splitText.lines, {
        yPercent: 0,
        clipPath: 'inset(-20% -10% -10% 0%)',
        opacity: 1,
        stagger: 0.12,
        duration: 1.6,
        delay: 0.5,
        // delay: isHomePage ? 0.5 : 0, // 0.5s delay only on home page
        ease: 'power3.out',
        scrollTrigger: {
          trigger: element,
          start: 'top 100%',
          end: 'bottom 100%',
          toggleActions: 'play none none reverse',
          once: true,
        },
      });
    });

    gsap.set(btnLinks, {
      yPercent: 100,
      clipPath: 'inset(0% 0% 120% 0%)',
      opacity: 1,
    });
    gsap.set(themeToggle, {
      yPercent: 100,
      clipPath: 'inset(0% 0% 120% 0%)',
      opacity: 1,
    });

    gsap.to(btnLinks, {
      yPercent: 0,
      clipPath: 'inset(-20% -10% -10% 0%)',
      opacity: 1,
      stagger: 0.12,
      duration: 1.6,
      delay: 0.5,
      ease: 'power3.out',
      scrollTrigger: {
        trigger: btnLinks[0],
        start: 'top 100%',
        end: 'bottom 100%',
        toggleActions: 'play none none reverse',
        once: true,
      },
    });

    gsap.to(themeToggle, {
      yPercent: 0,
      clipPath: 'inset(-20% -10% -10% 0%)',
      opacity: 1,
      stagger: 0.12,
      duration: 1.6,
      delay: 0.5,
      ease: 'power3.out',
      scrollTrigger: {
        trigger: themeToggle[0],
        start: 'top 100%',
        end: 'bottom 100%',
        toggleActions: 'play none none reverse',
        once: true,
      },
    });
  }
}

export function themeToggle() {
  const toggleBtn = document.querySelector('[data-theme-toggle]');
  const container = document.querySelectorAll('[data-theme-container]');

  if (!toggleBtn || container.length === 0) return;

  toggleBtn.addEventListener('click', () => {
    container.forEach((el) => el.classList.toggle('theme-dark'));
  });
}
